import React, { useState, useEffect } from "react";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import { Card, Form } from "react-bootstrap";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import LabelJson from "../../../Properties/LabelJson";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import {  isColor } from "../../../../Resources/globalFunctions";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { useTranslation } from "react-i18next";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { FontWeightListJson } from "../../../Properties/FontWeightList";
import { validateCustomControlId } from "../../RequestLogic";
import { updateUserId } from "../../Logic/UserIdLogic";
import Dots from "../../../../Icons/ThreeDots.jsx";
import MappingFile from "../../../../ReusableComponents/Mapping/MappingFile";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks.js";


const LabelProperties = (props) => {
  const contexts = GetStateFromData(props.containerId);
  const {
    updateJson,
    deleteControl,
    showAlertConfirmNotifications,
    fromQuickProps
  } = contexts.contexts;
  const { updatedComponentInFormState } = updateJson;

  const { t } = useTranslation(["styles", "translation"]);
  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
});
  const translationStyles1 = window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA";

  const [mappingField, setMappingField] = useState(false);

  useEffect(() => {
    if (fromQuickProps) setMappingField(true);
  }, [fromQuickProps]);

  const labelStyle = {
    FontColor: isColor(props.selectedControl.style.FontColor),
    BackColor: isColor(props.selectedControl.style.BackColor),
    FontSize: props.selectedControl.style.FontSize,
    FontFamily: props.selectedControl.style.FontFamily,
    FontWeight: props.selectedControl.style.FontWeight
  };

  if (props.selectedControl.style["Label"] === undefined) {
    props.selectedControl.style["Label"] = props.selectedControl.id;
  }

  var CollapsibleItems = [];
  Object.keys(LabelJson).forEach((key, index) => {
    var collapseComponentBody = [];
    Object.keys(LabelJson[key]).forEach((property) => {
      collapseComponentBody.push(prepareHtmlTag(property, ControlsMap[property].type));
    });
    CollapsibleItems.push(<CollapsibleComponent id={index + ""} heading={t(key)} body={collapseComponentBody} />);
  });

  function selectedOptionForLabel(selectedControlId, selectedControlLabel, ControlLabelKey) {
    const select = document.getElementById(selectedControlId + ControlLabelKey);
    const option_tag = select.options[select.selectedIndex];

    let temp_style = props.selectedControl.style;
    temp_style[ControlLabelKey] = option_tag.value;

    if(option_tag.value === "" || option_tag.value === "Select") {
      props.selectedControl.style[ControlLabelKey] =  "";
      props.selectedControl.style["renderTheme"] = !props.selectedControl.style?.["renderTheme"];
    } else {
      props.selectedControl.style[ControlLabelKey] = option_tag.value;
    }

    if (option_tag.value === "center") {
      props.selectedControl.style[ControlLabelKey] = "center";
    } else if (option_tag.value === "left") {
      props.selectedControl.style[ControlLabelKey] = "left";
    } else if (option_tag.value === "right") {
      props.selectedControl.style[ControlLabelKey] = "right";
    }

    if (option_tag.value === "Regular Italic") {
      props.selectedControl.style[ControlLabelKey] = 300;
      props.selectedControl.style["FontStyle"] = "italic";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "Semibold") {
      props.selectedControl.style[ControlLabelKey] = 600;
      props.selectedControl.style["FontStyle"] = "normal";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "Bold") {
      props.selectedControl.style[ControlLabelKey] = 700;
      props.selectedControl.style["FontStyle"] = "normal";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "Regular") {
      props.selectedControl.style[ControlLabelKey] = 300;
      props.selectedControl.style["FontStyle"] = "normal";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "Bold Italic") {
      props.selectedControl.style[ControlLabelKey] = 700;
      props.selectedControl.style["FontStyle"] = "italic";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "Light Italic") {
      props.selectedControl.style[ControlLabelKey] = 100;
      props.selectedControl.style["FontStyle"] = "italic";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "Semibold Italic") {
      props.selectedControl.style[ControlLabelKey] = 600;
      props.selectedControl.style["FontStyle"] = "italic";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "Light") {
      props.selectedControl.style[ControlLabelKey] = 100;
      props.selectedControl.style["FontStyle"] = "normal";
      props.selectedControl.style["WeightStyle"] = option_tag.value;
    } else if (option_tag.value === "" || option_tag.value === "Select") {
      props.selectedControl.style[ControlLabelKey] = "";
      props.selectedControl.style["FontStyle"] = "";
      props.selectedControl.style["WeightStyle"] = "";
    }

    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }


  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue = event?.key;
      if (propertyName === "CustomId") {
        if(event.target.selectionEnd === event.target.selectionStart){
          passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");
        }
        else {
          let stringArray = propertyValue.split("");
          stringArray.splice(event.target.selectionStart, event.target.selectionEnd - event.target.selectionStart);
          stringArray.splice(event.target.selectionStart, 0, ...event?.key.split(""));
          passValue = stringArray.join("");
        }
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(passValue, ControlsMap[propertyName]["regexType"])
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage"
        });
      } else {
        setAlertData({
          showConfirm: "showNotification",
          open: false,          
        });
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  function handleProperty(propertyName, propertyValue, event) {
    let shouldUpdate = true;

    if (propertyName === "CustomId") {
      if (propertyValue !== props.selectedControl.style.CustomId) {
        var element = event.target;
        var jsonToSend = {
          customControlId: propertyValue,
          controlid: props.selectedControl.id
        };
        validateCustomControlId(jsonToSend)
          .then((response) => {
            if (response && response.data && response.data.status === "0") {
              let temp_style = props.selectedControl.style;
              let updatedJson = updateUserId(
                props.selectedControl, // whole control Json
                propertyValue //newly defined id
              );
              if (updatedJson) {
                updatedComponentInFormState(props.selectedControl.id, updatedJson);
              }
            } else if (response && response.data && response.data.status === "-1") {

              showAlertConfirmNotifications({
                open: true,
                showConfirm: "showNotification",
                alertType: "error",
                alertContentKey: "CustomControl Id already Exist"
              });

              element.value = props.selectedControl.style.CustomId || "";
            }
          })
          .catch(function (error) {
            // handle error
            element.value = props.selectedControl.style.CustomId || "";

            showAlertConfirmNotifications({
              open: true,
              showConfirm: "showNotification",
              alertType: "error",
              alertContentKey: `Server side error ${error}`
            });
          });
      }
      shouldUpdate = false;
    } else if (propertyName === "Label" && !propertyValue) {
      setAlertData({
        open: true,
        showConfirm: "showNotification",
        alertType: "error",
        alertContentKey: "label is required",
        autoHideDuration: 6000
      });
      shouldUpdate = false;
      console.log("nkb props", props);
    }
    if (shouldUpdate) {
      let temp_style = props.selectedControl.style;
      temp_style[propertyName] = propertyValue;
      props.selectedControl.style[propertyName] = propertyValue;

      updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
    }
  }

  function handleToggle(e, field) {
    const value = e.target.value;
    props.selectedControl.style[field] = value;
    updatedComponentInFormState(props.selectedControl.id, props.selectedControl);
  }

  function mappingOpen() {
    setMappingField(true);
  }

  function prepareHtmlTag(key, type) {
    switch (type.toLowerCase()) {
      case "text":
        return (
          <div className={styles.parentDiv}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div className={styles.RightTextBox}>
              {key !== "Label" ? (
                <input
                  type="text"
                  aria-label={"Control Value1"}
                  className={[
                    styles.RightLabelTextBox,
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                  ].join(" ")}
                  defaultValue={props.selectedControl.style[key]}
                  key={props.selectedControl.id}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value, event)}
                />
              ) : (
                <input
                  type="text"
                  aria-label={"Control Value2"}
                  className={[
                    styles.RightLabelTextBox,
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                  ].join(" ")}
                  defaultValue={props.selectedControl.style["ButtonLabel"]}
                  key={props.selectedControl.id}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value, event)}
                />
              )}
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "text_top_bottom":
        return (
          <Form.Group className={styles.parentDivTopBottom}>
            <div>
              <span className={styles.topBottomLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            </div>

            {key === "Mapping" ? (
              <div style={{ display: "flex" }}>
                <input
                  type="text"
                  aria-label={"Control Value3"}
                  defaultValue={props.selectedControl.style[key]}
                  key={props.selectedControl.id + props.selectedControl.style[key]}
                  id={props.selectedControl.id + key}
                  className={[
                    styles.fullWidthTextBox,
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                  ].join(" ")}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value, event)}
                />
                <span
                  title={t("translation: View Data Objects")}
                  className={styles.mappingDotsIcon}
                  style={translationStyles1 ? { borderLeft: "1px solid #C4C4C4" } : null}
                  onClick={mappingOpen}
                >
                  <Dots />
                </span>
              </div>
            ) : (
              <div>
                <input
                  type="text"
                  aria-label={"Control Value4"}
                  defaultValue={props.selectedControl.style[key]}
                  key={props.selectedControl.id}
                  className={[
                    styles.fullWidthTextBox,
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                  ].join(" ")}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value, event)}
                />
              </div>
            )}

            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </Form.Group>
        );

      case "toggle":
        return (
          <div className={styles.parentDiv} style={translationStyles1 ? { display: "flex" } : null}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>

            <div className={translationStyles1 ? styles.toggleCheckboxArabic : styles.toggleCheckbox}>
              <ToggleBtn
                key={props.selectedControl.id}
                toggleState={props.selectedControl.style[key]}
                handleToggle={handleToggle}
                field={key}
              />
            </div>
          </div>
        );

      case "combo":
        return (
          <>
            <div className={styles.parentDiv} style={translationStyles1 ? { display: "flex" } : null}>
              <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>

              <div className={styles.RightLabelSelectForText}>
                {key !== "FontWeight" ? (
                  <>
                    <select
                      id={props.selectedControl.id + key}
                      className={styles.RightLabelSelectForText}
                      onChange={() => selectedOptionForLabel(props.selectedControl.id, ControlsMap[key].label, key)}
                      key={props.selectedControl.id}
                    >
                      {ControlsMap[key].children.map((optionTagData, optionCount) => {
                        if (ControlsMap[key].children[optionCount].Title) {
                          return (
                            <>
                              {ControlsMap[key].children[optionCount].Value === props.selectedControl.style[key] ? (
                                <option
                                  style={ControlsMap[key].children[optionCount]?.style}
                                  value={ControlsMap[key].children[optionCount].Value}
                                  selected
                                >
                                  {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}                                  
                                </option>
                              ) : (
                                <option
                                  style={ControlsMap[key].children[optionCount]?.style}
                                  value={ControlsMap[key].children[optionCount].Value}
                                >
                                  {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}                                  
                                </option>
                              )}
                            </>
                          );
                        }
                      })}
                    </select>
                  </>
                ) : (
                  <>
                    <select
                      id={props.selectedControl.id + key}
                      className={styles.RightLabelSelectForText}
                      onChange={() => selectedOptionForLabel(props.selectedControl.id, ControlsMap[key].label, key)}
                      key={props.selectedControl.id}
                    >
                      {props.selectedControl.style && props.selectedControl.style["FontFamily"] ? (
                        <>
                          {FontWeightListJson[props.selectedControl.style["FontFamily"]].children.map(
                            (optionTagData, optionCount) => {
                              if (
                                FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount]
                                  .Title
                              ) {
                                return (
                                  <>
                                    {FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount]
                                      .Value === props.selectedControl.style["WeightStyle"] ? (
                                      <option
                                        value={
                                          FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                            optionCount
                                          ].Value
                                        }
                                        selected
                                      >{t(`translation:${
                                        FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                          optionCount
                                        ].Title
                                      }`)}    
                                        
                                      </option>
                                    ) : (
                                      <option
                                        value={
                                          FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                            optionCount
                                          ].Value
                                        }
                                      >{t(`translation:${
                                        FontWeightListJson[props.selectedControl.style["FontFamily"]].children[
                                          optionCount
                                        ].Title
                                      }`)}                                         
                                      </option>
                                    )}
                                  </>
                                );
                              }
                            }
                          )}
                        </>
                      ) : (
                        <>
                          {FontWeightListJson["Open Sans"].children.map((optionTagData, optionCount) => {
                            if (FontWeightListJson["Open Sans"].children[optionCount].Title) {
                              return (
                                <>
                                  {FontWeightListJson["Open Sans"].children[optionCount].Value ===
                                  props.selectedControl.style["WeightStyle"] ? (
                                    <option
                                      value={FontWeightListJson["Open Sans"].children[optionCount].Value}
                                      selected
                                    >                                      
                                      {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}
                                    </option>
                                  ) : (
                                    <option value={FontWeightListJson["Open Sans"].children[optionCount].Value}>                                      
                                      {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}
                                    </option>
                                  )}
                                </>
                              );
                            }
                          })}
                        </>
                      )}
                    </select>
                  </>
                )}
              </div>
            </div>
          </>
        );

      case "colorpicker":
        return (
          <div className="colorpicker" key={props.selectedControl.id + key}>
            {t(`translation:${ControlsMap[key].label}`)}

            <ColorPicker
              color={labelStyle[key]}
              handleProperty={handleProperty}
              propertyName={key}
              key={props.selectedControl.id}
            />
          </div>
        );

      case "textbox":
        return (
          <div>
            <div className={styles.leftLabelForTextBox}>
              <div className={styles.labelForLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
                {key === "Label" ? <span className={styles.mandatory}> *</span> : null}
              </div>
              <input
                type="text"
                aria-label={"Control Value5"}
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id + props.selectedControl.style[key]}
                id={props.selectedControl.id + key}
                className={[
                  styles.textBoxLayoutForLable,
                  errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null
                ].join(" ")}
                onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                onBlur={(event) => handleProperty(key, event.target.value, event)}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "textarea":
        return (
          <div>
            <div
            >
              <p>{t(`translation:${ControlsMap[key].label}`)}</p>
            </div>

            <div>
              {key !== "Label" ? (
                <textarea
                
                  aria-label={props.selectedControl.style[key]}
                  defaultValue={props.selectedControl.style[key]}
                  key={props.selectedControl.id}
                  className={[
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null,
                    styles.checkBoxLayoutStyletextArea
                  ].join(" ")}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value)}
                ></textarea>
              ) : (
                <textarea
                
                  aria-label={props.selectedControl.style[key]}
                  defaultValue={props.selectedControl.style[key]}
                  key={props.selectedControl.id}
                  className={[
                    errorMessageId === props.selectedControl.id + key ? styles.inputBoxErrorStyle : null,
                    styles.checkBoxLayoutStyletextArea
                  ].join(" ")}
                  onKeyPress={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onKeyUp={(event) => validateRegex(key, event.target.value, event, props.selectedControl.id + key)}
                  onBlur={(event) => handleProperty(key, event.target.value)}
                ></textarea>
              )}
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );
      default:
        return null;
    }
  }

  return (
    <>
      <ControlLabel
        label="Label"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId={props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
        <Card.Body className={styles.cardBodySheetProperties}>
          {CollapsibleItems.map((item, index) => {
            return item;
          })}
        </Card.Body>
      </div>
      {mappingField && (
        <MappingFile
          stateOpen={true}
          setMappingField={setMappingField}
          controlData={props.selectedControl}
          elementType={props.selectedControl.elem_type}
        />
      )}
      {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }
    </>
  );
};

export default LabelProperties;
